@import "../../App.css";

.Body{
    background-color: rgb(255, 255, 255);
    align-self: center;
    width:100%;
    overflow:hidden;



}

.ItemsCarouselWrapper{

    
    width: 90vw;
    margin: 0 auto;
    margin-top: -70px;
}
.CarouselCard{
    box-sizing: border-box;
    padding-top:0%;
    
    
    height: 25vh;
   
    margin-left: 50px;
    margin-right: 50px;
    background-color: var(--secondary);
    border-radius: 0.4rem;
    box-shadow: 0 10px 20px -15px rgba(15, 72, 179, 0.2);
}

.CarouselCardActive{
    box-sizing: border-box;
    padding-top:10%;
    
    height: 30vh;
    width:110%;

    margin-top:-2.5vh;
    margin-left: -5%;
    background-color: var(--secondary);
    border-radius: 0.4rem;
    box-shadow: 0 10px 20px -15px rgba(15, 72, 179, 0.2);
    
}

.mainImg {
    height:20vh;
    object-fit: cover;
    overflow: hidden;
    
}


@media (max-width:900px){
    

    .CarouselCard,.CarouselCardActive{
        background-color: var(--secondary);
        border-radius: 0.4rem;
        box-shadow: 0 10px 20px -15px rgba(15, 72, 179, 0.2);
    overflow: hidden;

    }
    .CarouselCard{
        height: 250px;
      
        
    }
    .CarouselCardActive{

        height: 300px;
        width:110%;
        margin-top:0px;
        margin-left: 0%;

        
    }
    
