@import "./../../../../App.css";

.WorkComponant{
    display:flex;
    flex-direction:column;
    height:100%;
    width:100%;
    justify-content:space-between;
    overflow: hidden;
}

.Preview{
    background-color:var(--white);
    height:78%;
    
}

.WorkTitleContainer{
    display:flex;
    flex-direction:column;
  justify-content: flex-start;
    color:var(--white);
    height:22%;
    background-color:var(--orange);
  
    
}

.WorkArrow{
    
    height:25%;    
    align-self: center;
    padding-bottom: 4px;
    
    
}
.WorkTitle{
      
  
    align-self: center;

    font-size:1.2em;
    font-weight:bold;

}

@media (max-width:900px){
    .WorkTitle{
font-size:1em
    }

}